/**
 * Report specific SCSS files
 * These styles only apply on the report pages
 * We already import the main css file in the header
 * in the theme, so we want to minimise the redeclared styles
*/
/**
 * Colour variables.
*/
/* Override the Miso typeface  */
/*
 * top site nav styles
 * we use these so we still have nav options
 * to the rest of the site
*/
.report-site-nav-menu {
  float: right; }
  .report-site-nav-menu a {
    color: #111; }
  .report-site-nav-menu button.toggle {
    display: none; }
  .report-site-nav-menu ul.sub-menu {
    background-color: #fff; }

div.report-page {
  max-width: 85rem;
  margin: 0 auto;
  overflow: hidden; }

.fog-of-enactment {
  /*
    these are overrides to remove whitespace between the cover
    image and the top of the page
  */ }
  .fog-of-enactment p,
  .fog-of-enactment li {
    font-size: 18px; }
  .fog-of-enactment h1,
  .fog-of-enactment h2,
  .fog-of-enactment h3,
  .fog-of-enactment h4,
  .fog-of-enactment h5,
  .fog-of-enactment h6 {
    font-family: Lato, sans-serif; }
  .fog-of-enactment .wp-block-cover__inner-container h2,
  .fog-of-enactment .wp-block-cover__inner-container h3 {
    padding-left: 1rem; }
  .fog-of-enactment .item-button {
    display: block; }
  .fog-of-enactment nav {
    background-color: #eee;
    padding: 2rem;
    /* we use z-index, to account for cover image clipping the sidebar */
    z-index: 1000;
    position: fixed;
    overflow: auto;
    width: 20rem;
    height: 100vh;
    transition: transform 0.2s; }
    .fog-of-enactment nav .logo {
      padding-top: 1em;
      padding-bottom: 1rem;
      padding-left: 1rem; }
    .fog-of-enactment nav .tocToggle {
      display: none; }
    .fog-of-enactment nav .toc {
      position: relative; }
      .fog-of-enactment nav .toc button {
        display: none; }
    .fog-of-enactment nav ol {
      margin-top: 0.5rem;
      margin-left: 0.5rem;
      list-style: none;
      margin-top: 0.5rem; }
    .fog-of-enactment nav ol li {
      font-size: 0.9rem;
      text-transform: uppercase;
      padding: 0.3rem; }
    .fog-of-enactment nav ol li a {
      text-decoration: none;
      color: #111; }
      .fog-of-enactment nav ol li a:hover {
        text-decoration: underline; }
    .fog-of-enactment nav details {
      margin-top: 0.5rem; }
    .fog-of-enactment nav details > summary {
      list-style: none;
      cursor: pointer; }
  .fog-of-enactment .footnotes p,
  .fog-of-enactment .footnotes li {
    font-size: 0.8rem; }
  .fog-of-enactment article {
    padding-top: 0;
    margin-left: 20rem; }
    .fog-of-enactment article section {
      padding-top: 0;
      /* add max width for readability */ }
      .fog-of-enactment article section h2,
      .fog-of-enactment article section h3 {
        padding-left: 2rem; }
      .fog-of-enactment article section li > h3,
      .fog-of-enactment article section li > p {
        padding-left: 0; }
      .fog-of-enactment article section p {
        max-width: 50rem;
        width: 60%;
        padding-left: 2rem; }
      .fog-of-enactment article section dl,
      .fog-of-enactment article section ol,
      .fog-of-enactment article section ul {
        max-width: 50rem;
        padding-left: 2rem;
        -webkit-padding-start: 5%;
        width: 60%; }
      .fog-of-enactment article section li {
        max-width: 45rem; }
        .fog-of-enactment article section li p {
          width: 100%; }
      .fog-of-enactment article section .sidenote {
        float: right;
        clear: right;
        margin-right: -40%;
        width: 30%;
        margin-top: 0.3rem;
        font-size: 0.8rem;
        line-height: 1.3;
        vertical-align: baseline;
        position: relative;
        margin-bottom: 1rem; }
        .fog-of-enactment article section .sidenote p,
        .fog-of-enactment article section .sidenote li {
          width: 100%;
          display: inline;
          padding-left: 0;
          font-size: 0.8rem; }
      .fog-of-enactment article section .nv-content-wrap {
        padding-top: 0;
        margin-top: 0; }

/*
colwidth is a class to handle SVG graphics - without this, they default
to being around 100px wide. This scales them back up
*/
figure.colwidth img {
  width: 100%;
  max-width: 50rem; }

.wp-block-image {
  margin-bottom: 4rem;
  /*
  we add the clear both to account for spaces where we have long sidenotes
  that overlap with images. this makes sure we have the whitespace
  */
  clear: both; }

.wp-block-image figcaption {
  font-size: 0.8rem;
  text-align: center;
  float: none; }

/*
override on the footer to push it over enough to account
our static sidebar.
*/
.site-footer {
  padding-left: 20rem;
  max-width: 85rem;
  margin: 0 auto;
  overflow: hidden; }

@media (max-width: 1024px) {
  /*
  visually hidden class to avoid touches/clicks on the site
  nav menu being triggered when trying to click on the
  report table of contents
  https://www.a11yproject.com/posts/how-to-hide-content/
*/
  .visually-hidden {
    clip: rect(0 0 0 0);
    clip-path: inset(50%);
    height: 1px;
    overflow: hidden;
    position: absolute;
    white-space: nowrap;
    width: 1px; }
  .item-button {
    display: inline-block; }
  .report-site-nav-menu {
    /*
    replicate the look and feel of the usual disclosure button for the site nav
    */ }
    .report-site-nav-menu button.toggle {
      display: block;
      position: absolute;
      top: 20px;
      right: 20px;
      background-color: #fff;
      border: 1px solid black; }
      .report-site-nav-menu button.toggle .icon-bar {
        background-color: black; }
    .report-site-nav-menu .nav-menu-primary {
      position: fixed;
      top: 5rem;
      right: 6px;
      height: 90vh;
      width: 90%;
      background-color: #fff;
      overflow: auto;
      transition: opacity 0.3s ease-in-out, visibility 0.3s ease-in-out;
      border: none;
      z-index: 999;
      padding: 2rem 1rem 1rem 1rem;
      opacity: 0;
      visibility: hidden;
      border-radius: 5px;
      /* overrides to show the full menu on mobile */
      /* more overrides */ }
      .report-site-nav-menu .nav-menu-primary .nav-ul {
        display: block; }
      .report-site-nav-menu .nav-menu-primary .sub-menu {
        visibility: visible;
        position: inherit;
        top: auto;
        opacity: 1;
        border: none;
        box-shadow: none; }
    .report-site-nav-menu .visible {
      opacity: 1;
      visibility: visible; }
  .fog-of-enactment .logo {
    max-width: 10rem;
    padding-top: 0; }
  .fog-of-enactment button.tocToggle {
    position: fixed;
    display: block;
    bottom: 30px;
    right: 30px;
    background-color: #65B54C;
    padding: 1rem; }
  /*
  we don't show this by default on mobile, but we fade it in
  when triggering the table of contents toggle
  */
  .fog-of-enactment nav {
    /* negate the large screen fixed menu styles */
    width: 100%;
    height: auto;
    position: initial;
    padding-bottom: 0;
    padding-top: 0.5rem; }
    .fog-of-enactment nav .logo {
      padding-top: 0;
      padding-bottom: 0;
      padding-left: 0; }
    .fog-of-enactment nav .toc {
      position: fixed;
      bottom: 30px;
      right: 30px;
      height: 70vh;
      width: 15rem;
      background-color: #65B54C;
      transition: opacity 0.3s ease-in-out, visibility 0.3s ease-in-out;
      border: none;
      z-index: 999;
      border-radius: 5px;
      padding: 2rem 1rem 1rem 1rem;
      opacity: 0;
      visibility: hidden;
      color: #fff;
      font-weight: bold;
      overflow: auto; }
      .fog-of-enactment nav .toc button {
        display: block;
        position: absolute;
        top: 10px;
        right: 10px; }
      .fog-of-enactment nav .toc a {
        color: #fff; }
    .fog-of-enactment nav .visible {
      opacity: 1;
      visibility: visible; }
  /* negate the large screen margin for the fixed menu */
  .fog-of-enactment article {
    margin-left: 0rem; }
  /*
  negate the large screen override for the footer as we don't have
  a fixed sidebar to compensate for
  */
  .site-footer {
    padding-left: 0; } }

/*
Override for the scroll to top button. Most of the time we need it,
but on the report page we don't.
*/
#scroll-to-top {
  display: none; }
